import '../App.css'
import HeroSection from '../components/HeroSection'
import { Link } from 'react-router-dom';
import web from '../images/logo3.jpg'
import '../pages/Service.css'

import rgreen from "../images/robog.jpg"

import ElectricCarIcon from '@mui/icons-material/ElectricCar';
import FitnessCenterIcon from '@mui/icons-material/FitnessCenter';
import SportsEsportsIcon from '@mui/icons-material/SportsEsports';

import BatteryChargingFullIcon from '@mui/icons-material/BatteryChargingFull';
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';
import ScienceIcon from '@mui/icons-material/Science';


import AssignmentTurnedInIcon from '@mui/icons-material/AssignmentTurnedIn';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import BalanceIcon from '@mui/icons-material/Balance';
import BuildIcon from '@mui/icons-material/Build';
import MoodIcon from '@mui/icons-material/Mood';
import AgricultureIcon from '@mui/icons-material/Agriculture';

import { ToastContainer,toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import React, { useRef } from 'react';
import emailjs from 'emailjs-com';
import{ init } from 'emailjs-com';
init("user_mNF2hAyVwCH02WN5e7WQV");



function Home() {

  const form = useRef();

  const sendEmail = (e) => {
    //const notify = () => toast("Sending...");
    e.preventDefault();

    emailjs.sendForm("service_08qhiud", "template_kxor1li", form.current, "user_mNF2hAyVwCH02WN5e7WQV")
      .then((result) => {
          console.log(result.text);
          toast.success('Email Send',{
            position:"top-right",
            autoClose: 5000,
            hideProgressBar: false,
            newestOnTop:false,
            closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          })
      }, (error) => {
          console.log(error.text);
          toast.warn('Error',{
            position:"top-right",
            autoClose: 5000,
            hideProgressBar:false,
            newestOnTop:false,
            closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          });
        });
        e.target.reset()
    
          }
  return(
        <>
        <HeroSection/>


        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
               <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative" >


               <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
               <div className="row ">
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                    <h1> 
                       <strong></strong> <strong className="brand-name">AIRbots</strong>
                    </h1>
                    <h2 className="my-3">
                    An emerging startup that seeks to provide automation and robotics solutions by providing products which accelerates business development and is economically viable even for small business
                    </h2>
                    <div className="mt-3" style={{padding:'5%'}}>
                        {/* <Link to='/' className="btn-get-started">
                          click
                        <Link> */}
                    </div>
                </div>

                <div className="col-lg-6 order1 order-lg-2 header-mg">
                    <img src={web} className="img-fluid animated " alt="img"/>
                </div>
                </div>
            </div>
          </div>
        </div>
      </section> 
    
    </div>
    </div>
    </div>
    </div>


    <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              

        <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
          <div className="service">
            <div className="row" style={{padding:'3%'}}>
              
            <h1 className="text-center " style={{padding:'5%'}}>Why Us</h1>
            <div className="row" style={{padding:'3%'}}>
          <div className="col-4 " >
          <p4 className="service"><AssignmentTurnedInIcon fontSize= "large" color = "success"/> Quick Deloyment </p4>
          </div>
          <div className="col-4">
        <p4 className="service"><AutorenewIcon fontSize= "large" color = "success"/> Swappable Battery</p4>
        </div>
        <div className="col-4">
        <p4 className="service"><BalanceIcon fontSize= "large" color = "success"/>Cost Effective </p4>
        </div>
        </div>
        </div>
        <div className="row" style={{padding:'3%'}}>
        <div className="col-4">
        <p4 className="service"><BuildIcon fontSize= "large" color = "success"/> Tailored Solution</p4>
        </div>
        <div className="col-4">
        <p4 className="service"><MoodIcon fontSize= "large" color = "success"/> Low Training time</p4>
        </div>
        <div className="col-4">
        <p4 className="service"><AgricultureIcon fontSize= "large" color = "success"/> Made in India</p4>  
        </div>
        </div>
       
        

          </div>
           </div>
          </div>
        </div>
      </div>

               
 



               <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
               <div className="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative" >
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column" >
                <h1 className="display-4 fw-bold lh-1"style={{ padding: '5%' }}>Feature</h1>
                <div className="service" style={{ padding: '5%' }}>
                 
        <p ><ElectricCarIcon fontSize= "large" color = "success"/> Fully Electric vehicle</p>
        <p ><FitnessCenterIcon fontSize= "large" color = "success"/> Payload of more than 400kg*</p>
        <p className="lead"><SportsEsportsIcon fontSize= "large" color = "success"/> Easy to Control </p>
        <p className="lead"><BatteryChargingFullIcon fontSize= "large" color = "success"/> Run time of 5 hours*</p>
        <p className="lead"><CurrencyRupeeIcon fontSize= "large" color = "success"/> Economical solution</p>
        <p className="lead"><ScienceIcon fontSize= "large" color = "success"/> Customisation according to client</p>  
        <p1>*These setting can be adjusted according to customer needs</p1>
        </div>
                
                </div>

                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column"style={{ padding: '5%' }}>
                <img src={rgreen} className="img-fluid animated " alt="img" style={{ paddingTop: '5%' }}/>
                </div>
                </div>
            </div>
          </div>
        </div>

      
      

      
        <ToastContainer/>

        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              

        <div class="row g-0 border rounded overflow-hidden flex-md-row mb-4 shadow-sm h-md-250 position-relative">
        <div class="col p-4 d-flex flex-column position-static">
      <div className="=my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>
      
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
         
            <form ref={form}onSubmit={sendEmail}>
              <div class="mb-3">
                <label for="exampleInputEmail1" class="form-label">
                  FullName
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputEmail1"
                  name="user_name"
                  //value={data.fullname}
                  onChange={InputEvent}
                  placeholder="Enter your name"
                  />
                <label for="exampleInputEmail1" class="form-label">
                  Phone
                </label>
                <input
                  type="number"
                  class="form-control"
                  id="exampleInputEmail1"
                  name="user_phone"
                  //value={data.phone}
                  onChange={InputEvent}
                  placeholder="Phone"
                />
                <label for="exampleInputEmail1" class="form-label">
                  Email address
                </label>
                <input
                  type="email"
                  class="form-control"
                  id="exampleInputEmail1"
                  name="user_email"
                  //value={data.email}
                  onChange={InputEvent}
                  placeholder="email"
                />
               
              </div>
              <div class="mb-3">
                <label for="exampleFormControlTextarea1" class="form-label">
                  Message
                </label>
                <textarea
                  type="text"
                  class="form-control"
                  id="exampleFormControlTextarea1"
                  name="message"
                  //value={data.message}
                  onChange={InputEvent}
                  placeholder="Message"
                ></textarea>
              </div>
              <div className="col-12">
              <button type="submit" value="Send" class="btn btn-outline-primary">
                Submit
              </button>
              </div>
            </form>
           
          </div>
        </div>
      </div>

</div>
</div>

</div>
</div>
</div>
      <ToastContainer/>



        </>
    )
    
}

export default Home;