import React ,{useState,useEffect}from 'react'
import {Link} from 'react-router-dom'
import { Button } from './Button';
import './Navbar.css'
import logo from '../images/logo3.jpg'
function Navbar() {
    const [click,setClick]=useState(false);
    const [button1,setButton]=useState(true)

    const handleClick =() => setClick(!click);
    const closeMobileMenu=()=> setClick(false);


    const showButton = () =>{
        if(window.innerWidth <=900){
            setButton(false)

        }else{
            setButton(true);
        }
    }

    useEffect(()=>{
        showButton()
    },[])

    window.addEventListener('resize',showButton);
  return (
    <>
    <nav1 className='navbar1'>
        <div className='navbar1-container1'>


        <Link to='/' className="navbar-logo1" >
            {/* Test <i className='fab fa-typo3'/> */}
            <img src={logo} alt="" width="150" height="65" />
        </Link>
        <div className='menu-icon1' onClick={handleClick}>
            <i className={click ? 'fas fa-times': 'fas fa-bars'}/>

        </div>
        <ul className={click ? 'nav1-menu1 active': 'nav1-menu1'}>
        <li className='nav1-item1'>
            <Link to='/' className='nav1-links1' onClick={closeMobileMenu}>
                Home
            </Link>

        </li>
        <li className='nav1-item1'>
            <Link to='/service' className='nav1-links1' onClick={closeMobileMenu}>
                Features
            </Link>

        </li>
        <li className='nav1-item1'>
            <Link to='/About' className='nav1-links1' onClick={closeMobileMenu}>
                About
            </Link>

        </li>
        <li className='nav1-item1'>
            <Link to='/contact' className='nav1-links1' onClick={closeMobileMenu}>
                Contact
            </Link>

        </li>

        <li >
            {/* <Link to='<a href="tel: 9930652916">' className='nav1-links1-mobile' onClick={closeMobileMenu}>
                CALL
            </Link> */}
            <a href="tel: 7012723834" className='nav1-links1-mobile' onClick={closeMobileMenu}>
              <Button>Call</Button>
              </a>

            {/* <a href="tel: 9930652916">
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                //   style={{
                //     color: "rgb(0, 195, 255)",
                //     border: "1.5px solid  rgb(0, 195, 255)",
                //   }}
                >
                  <strong>Call</strong>
                </button>{" "}
              </a> */}

        </li>
        </ul>
        
        {button1 && <Button buttonStyle='btn1--outline'>Call</Button>}
        </div>
    </nav1>
    </>
  )
}

export default Navbar